import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { HttpClient } from '@angular/common/http';


/**
 * Generated class for the TopappPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-topapp',
  templateUrl: 'topapp.html',
})
export class TopappPage {

  d : any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams, private http : HttpClient) {
     this.d = {
       'ours' : '',
       'trackCensoredName' : '',
       'primaryGenreName' : '',
       'description' : '',
       'artwordUrl100' : 'https://jkur.com.au/app4apps/server/blank.jpg'
    }
  }

  ionViewDidLoad() {  let that=this;
       let url = 'https://jkur.com.au/app4apps/server/topapp.php?rnd='+Math.random();
        this.http.get(url).subscribe(
         data => {
      	let s = JSON.stringify(data);
                that.d = JSON.parse(s);
         },
         err => {
         }
        ); 
  }

}



