import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { OneSignal } from '@ionic-native/onesignal';
import { HttpClient } from '@angular/common/http';
import { TopappPage } from '../topapp/topapp';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {

  content : any = 'Loading ...';
  banner : any = 'https://jkur.com.au/app4apps/server/sponsor.png';
  pushid : any = '';
  thisversion : any = 1;

  constructor(public navCtrl: NavController, private http : HttpClient, private alertCtrl : AlertController, private oneSignal: OneSignal) {

  }

 ionViewDidEnter()
  { 

   let that=this;
       let url = 'https://jkur.com.au/app4apps/server/home.php?rnd='+Math.random();
        this.http.get(url).subscribe(
         data => {
 	let s = JSON.stringify(data);
                let d = JSON.parse(s);
           that.content = d.content;
           let v = parseInt(d.version);
            if (v > that.thisversion)
               that.versionmessage(v);
            that.getpushinfo();
            
         },
         err => {
         }
        ); 
  }


  getpushinfo()
  {
     this.oneSignal.startInit('045e76cf-56d9-4af4-9d39-aa8742a00895', '981456573821'); 
 
     this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);
     this.oneSignal.endInit();
     let i = this.oneSignal.getIds();
     i.then( data => {
 
        let that=this;
        let url = 'https://jkur.com.au/app4apps/server/pushtoken.php?pushid='+data.userId;
        window.localStorage.setItem('pushid',data.userId);
        this.http.get(url).subscribe(
         data => {
         },
         err => {
         }
        ); 
        
     });
  }     

 versionmessage(a) {
      this.thisversion = a;
      let that=this;
      let alert = this.alertCtrl.create({
      title: 'New Version Available',
      subTitle: 'Please go to the App Store or Google Play Store to get the latest version of this App',
      buttons: [
        {
          text: 'OK',
          role: 'cancel',
          handler: () => {
          }
        }
        
      ]
    });
    alert.present();
 }
  
 topapp()
 {
    this.navCtrl.push(TopappPage);
 }

 prevapps()
 {

 }

 dologout()
 {

 }

 myacc()
 {

 }

}



